import React from "react"
import { Container, Button, Col, Media, Image, Row } from "react-bootstrap"
import "./styles/how-it-works.styles.scss"
import { StaticImage } from "gatsby-plugin-image"

const HowItWorks = () => (
  <Container fluid className="home-container how-it-works p-0">
    <Container fluid className=" py-xl-0">
      <Container fluid="xl" className="py-xl-6 d-flex flex-column ">
        <h1 className="h1-header"> How it works </h1>
        <Row>
          <Col md={9}>
            <p>
              Jump Loans makes borrowing easy and fast when you need it most. No
              more applying for loan after loan. Once you’re approved for a Jump
              Loans line of credit, you can request money online and have it
              transferred to your banking account. Here’s how it works.
            </p>
            <h3 className="h3-sub-header">
              You will receive a $400 to $1,100 Jump Loans line of credit, upon
              approval.
            </h3>
            <ol className="main-ol">
              <li>
                <p>
                  Apply in minutes using our secure online application process
                </p>
              </li>
              <li>
                <p>
                  Get approved <br></br>To qualify for a Jump Loan line of
                  credit you will need:
                  <ul className="seconday-ul mt-2">
                    <p>
                      - A verifiable source of income directly deposited into
                      your checking account
                    </p>
                    <p>
                      - An open checking account that meets our minimum-opened
                      duration requirement
                    </p>
                    <p>- A proven United States citizenship</p>
                    <p>- Proof that you’re at least 18 years old</p>
                  </ul>
                </p>
              </li>
              <li>
                <p>
                  Receive an acceptance decision from us in seconds. Your Jump
                  Loans line of credit will be funded into your online account
                  within 2 days.
                </p>
              </li>
              <li>
                <p>Request the amount you’d like to transfer.</p>
              </li>
              <li>
                <p>
                  Deducting from your Jump Loans line of credit account, we’ll
                  deliver only the amount you request straight into your bank
                  account the same day.
                </p>
              </li>
              <li>
                <p>
                  You can request as many transfers as you need up to your
                  credit limit.
                </p>
              </li>
              <li>
                <p>
                  As you repay your transfers back into your Jump Loans line of
                  credit account, your line of credit balance will replenish.
                </p>
              </li>
            </ol>
          </Col>
        </Row>
      </Container>
    </Container>
    <Container fluid className="solid-bg-container py-xl-6 pt-5">
      <Container fluid="xl" className="py-xl-6">
        <Row className="text-center">
          <Col
            md={11}
            className="justify-content-center text-center px-5 m-auto"
          >
            <h1 className="h1-header text-white"> Your Jump Loans payments </h1>
            <h3 className="py-4 font-weight-light m-0">
              At Jump Loans, we make paying back your transfers quick and
              simple. Once you’ve received your Jump Loans line of credit,
              you’ll have a minimum payment due at the end of each billing
              cycle, which is 10% of your line of credit. You’ll only pay
              interest only on what you transfer.{" "}
            </h3>
            <h3 className="py-4 font-weight-light m-0">
              You can choose to make payments weekly, bi-weekly, or monthly. If
              you run into payment issues, just let us know. We’ll be happy to
              work with you to accommodate your needs.{" "}
            </h3>
            <h3 className="py-4 font-weight-light m-0">
              Keep in mind, the faster you pay off what you owe, the more your
              line of credit balance increases. You can always pay more than
              your minimum payment, and you won’t be penalized for paying your
              transfer back early.{" "}
            </h3>
          </Col>
        </Row>
      </Container>
    </Container>
    <Container fluid className=" py-xl-0">
      <Container
        fluid="xl"
        className="py-xl-6 d-flex flex-column pt-5 payment-options"
      >
        <Row>
          <Col md={{ order: "first" }} className="text-center">
            <h1 className="h1-header"> Payment options</h1>
            <h3 className="h3-sub-header">
              You make pay back your transfers using the following methods:
            </h3>
          </Col>
        </Row>
        <Row className="mt-5">
          <Col>
            {" "}
            <Media className="mb-5">
              <StaticImage
                className="media-img"
                src="../images/icon_payment_cal.png"
                alt="Jump Mobile App"
                placeholder="blurred"
                width={150}
                height={150}
                fit="cover"
              />

              <Media.Body className="align-self-center media-text">
                <h3 className="mb-1">Automatic deduction</h3>
                <p>
                  We’ll automatically deduct your minimum payment on each due
                  date. You can also choose to make additional payments by
                  logging into our online customer portal and pay back your
                  transfer faster.
                </p>
              </Media.Body>
            </Media>
            <Media className="mb-5">
              <StaticImage
                className="media-img"
                src="../images/icon_payment_card.png"
                alt="Jump Mobile App"
                placeholder="blurred"
                width={150}
                height={150}
                fit="cover"
              />
              <Media.Body className="align-self-center media-text">
                <h3 className="mb-1">Debit card payments</h3>
                <p>
                  You can use our online customer portal to make a payment with
                  your debit card.
                </p>
              </Media.Body>
            </Media>
          </Col>
          <Col>
            <Media className="mb-5">
              <StaticImage
                className="media-img"
                src="../images/icon_payment_Phone.png"
                alt="Jump Mobile App"
                placeholder="blurred"
                width={150}
                height={150}
                fit="cover"
              />
              <Media.Body className="align-self-center media-text">
                <h3 className="mb-1">One-time payment</h3>
                <p>
                  You can make a one-time payment by logging into our online
                  customer portal at any given time during your payment cycle.
                  Or you can contact us at (850) 990-JUMP to schedule it over
                  the phone.
                </p>
              </Media.Body>
            </Media>
            <Media className="mb-5">
              <StaticImage
                className="media-img"
                src="../images/icon_payment_paypal.png"
                alt="Jump Mobile App"
                placeholder="blurred"
                width={150}
                height={150}
                fit="cover"
              />
              <Media.Body className="align-self-center media-text">
                <h3 className="mb-1">PayPal payments</h3>
                <p>
                  You can use ouronline customer portal to make a payment with
                  your debit card.
                </p>
              </Media.Body>
            </Media>
          </Col>
        </Row>
        <Row>
          <Col className="px-5 py-3">
            <p className="px-5 text-center">
              If at any time, you’re unable to make your minimum payment, call
              us immediately at <strong>(850) 990-JUMP</strong> so we can make
              proper arrangements.
            </p>
          </Col>
        </Row>
      </Container>
    </Container>
  </Container>
)

export default HowItWorks
